<template>
    <div>
        <u-switch v-model="m1" :loading="loading">
            <span slot="open">开</span>
            <span slot="close">关</span>
        </u-switch>
        <div @click="m1 = !m1">toggle</div>
        <div @click="loading = !loading">loading</div>
        <br><br>
        <u-switch size="large" loading></u-switch>
        <u-switch></u-switch>
        <u-switch size="small" v-model="m1" :loading="loading"></u-switch>
        <br><br>
        <u-switch>
            <span slot="open">开</span>
            <span slot="close">关</span>
        </u-switch>
        <u-switch>
            <u-icon type="md-checkmark" slot="open"></u-icon>
            <u-icon type="md-close" slot="close"></u-icon>
        </u-switch>
        <br><br>
        <u-switch size="large">
            <span slot="open">开启</span>
            <span slot="close">关闭</span>
        </u-switch>
        <u-switch size="large" v-model="m1" :loading="loading">
            <span slot="open">ON</span>
            <span slot="close">OFF</span>
        </u-switch>
        <br><br>
        <u-switch :disabled="disabled"></u-switch>
        <u-button type="primary" @click="disabled = !disabled">Toggle Disabled</u-button>

        <u-switch width="85px" height="26px" :skin="[img11, img12]" trueRule="1,3" falseRule="1,3||20,10,20,10"></u-switch>
    </div>
</template>
<script>
    import img11 from './img/carddating/btn_cancel_seat.png';
    import img12 from './img/carddating/btn_on.png';
    export default {
        data () {
            return {
                m1: true,
                disabled: true,
                loading: false,
                img11,
                img12,
            };
        },
        methods: {
            change (status) {
                console.log(status);
            }
        }
    };
</script>



